  methods: {
    drawChart(option) {
      const opt = option || {
        title: {
          text: '2014-2017上半年各省票房收入桑基图',
          subtext:'数据来源：国家电影专资办'
        },
        tooltip: {
          trigger  : 'item',
          formatter: data => data.name + '：' + data.value,
          triggerOn: 'mousemove'
        },
        color: ['#3ca8d3'],
        series: [{
          type: 'sankey',
          layout: 'none',
          top: 40,
          bottom: 10,
          left: 10,
          right: 110,
          data: this.nodes,
          links: this.links,
          itemStyle: {
            normal: {
              borderWidth: 1,
              borderColor: '#aaa'
            }
          },
          lineStyle: {
            normal: {
              curveness: 0.5
            }
          }
        }]
      }

      this.chart.setOption(opt)
      this.onResize()
    },
    onResize() {
      if(this.chart) _.debounce(this.chart.resize, 100)()
    },
  },
  created() {

  },
  mounted() {
    this.chart = echarts.init(this.$refs.echartsSankeyDiagram, 'shine')
    _.delay(this.drawChart, 1)
