<template>
<div>
  <v-navigation-drawer app fixed clipped v-model='drawer' :width='width'>
    <v-list color='primary'>
      <template v-for='(x, i) in items'>
        <v-list-tile v-if='!x.group' :key='x.to' :to='x.to'>
          <v-list-tile-content>
            <v-list-tile-title v-text='x.title' />
          </v-list-tile-content>
        </v-list-tile>

        <v-list-group v-else :key='x.title' v-model='x.active' :prepend-icon='x.action' no-action>
          <template v-slot:activator>
            <v-list-tile>
              <v-list-tile-content>
                <v-list-tile-title v-text='x.title' />
              </v-list-tile-content>
            </v-list-tile>
          </template>

          <v-list-tile v-for='y in x.subItems' :key='y.title' :to='y.to'>
            <v-list-tile-content>
              <v-list-tile-title v-text='y.title' />
            </v-list-tile-content>

            <v-list-tile-action>
              <v-icon v-text='y.action' />
            </v-list-tile-action>
          </v-list-tile>
        </v-list-group>
      </template>
    </v-list>
  </v-navigation-drawer>
</div>
</template>


<script>
export default {
  data: () => ({
    drawer: false,
    items: [{
      title: 'Main',
      to: '/'
    }, {
      title : 'submenu',
      group : true,
      active: false,
      action: 'thumb',
      subItems: [
        { title: 'Jack',    to: '/jack' },
        { title: 'Matt',    to: '/matt' },
        { title: 'Xiaoshu', to: '/xiaoshu' },
        { title: 'Kevin', to: '/kevin' },
        { title: 'Water',   to: '/water' },
        { title: 'Energy',  to: '/energy' },
      ]
    }],
  }),
  computed: {
    width() {
      return _.max([window.innerWidth * 0.15, 300])
    }
  },
  beforeMount() {
    this.drawer = this.$vuetify.breakpoint.lgAndUp
    
    this.$root.$on('onDrawer', () => {
      this.drawer = !this.drawer
    })
  }
}
</script>
