<template>
<v-app>
  <AppNavigationDrawer />

  <AppToolbar />

  <v-content>
    <v-layout justify-center>
      <v-flex xs12>
        <v-slide-x-transition mode='out-in'>
          <router-view />
        </v-slide-x-transition>
      </v-flex>
    </v-layout>
  </v-content>
</v-app>
</template>


<script>
import AppNavigationDrawer from '@/components/shared/NavigationDrawer'
import AppToolbar          from '@/components/shared/Toolbar'

export default {
  data: () => ({

  }),
  created() {
    this.$vuetify.theme.primary = '#303F9F'
    this.$vuetify.theme.golden  = '#C7A754'
  },
  watch: {
    '$vuetify.breakpoint'(to) {
      if(to.mdAndDown) _.debounce(() => this.drawer = false, 100)()
    }
  },
  components: {
    AppToolbar,
    AppNavigationDrawer
  }
}
</script>
